import React from 'react'


import BlogMenu from '../BlogMenu';

import Norway from "../Norway"
import Footer from "../Footer"

class NorwayComponent extends React.Component {  
   render() {
    return (
      <div>
      <BlogMenu />
     
       <div className="note">
          <div class="logo_text">
              <h3>Ксения - путешественница</h3>
              <h4>Истории о моих путешествиях</h4>
          </div>
        <div className="country_grid">
        <div>  <Norway /> </div>  
        </div>
     </div>
     <Footer />
     </div>
    )
   }

  }


export default NorwayComponent