import React from "react"

import "../sass/main.sass"

import BlogMenu from "./BlogMenu"
import Argentina from "./Argentina"
import Mexico from "./Mexico"
import Ecuador from "./Ecuador"
import Peru from "./Peru"
import Colombia from "./Colombia"
import Costarica from "./Costarica"
import Chile from "./Chile"
import Norway from "./Norway"
import Europe from "./Europe"
import Brasil from "./Brasil"
import Italy from "./Italy"
import Footer from "./Footer"

const BlogContent = (props) => {
    return(
 <div>
     <BlogMenu />
       
        <div className="note">
        <div class="logo_text">
            <h3>Ксения - путешественница</h3>
            <h4>Истории о моих путешествиях</h4>
        </div>
            <div className="note_grid">
                <div className="note_text">
                    <h3>{props.title}</h3>
                    <p dangerouslySetInnerHTML={props.content}></p>
                    <p>Дата: {props.date}</p>
                </div>
                <div class="note_column">
                    <h3>Другие записи:</h3>
                    {props.country === "argentina" ? <Argentina /> : null }
                    {props.country === "mexico" ? <Mexico /> : null }
                    {props.country === "ecuador" ? <div> <Ecuador /> <Costarica />  <Norway /> <Europe /> <Mexico />  </div> : null }
                    {props.country === "peru" ? <div> <Peru /> <Argentina /> </div>: null }
                    {props.country === "colombia" ? <div> <Colombia /> <Costarica />  <Norway /> <Europe /> <Mexico />  </div>  : null }
                    {props.country === "costarica" ? <div> <Costarica />  <Norway /> <Europe /> <Colombia /> <Mexico /> </div> : null }
                    {props.country === "chile" ? <div> <Chile /> <Mexico /> </div> : null }
                    {props.country === "norway" ? <div>    <Norway /> <Europe /> <Colombia /> <Costarica /> <Peru /> <Mexico />  </div> : null }
                    {props.country === "europe" ? <div> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }
                    {props.country === "brasil" ? <div> <Brasil /> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }
                    {props.country === "italy" ? <div> <Italy /> <Brasil /> <Europe />  <Colombia /> <Costarica /> <Peru /> <Mexico /> </div> : null }
                </div>
            </div>
        </div>
        <Footer />
</div>
    )
}

export default BlogContent;