import React from 'react'

import map from "../img/map.png"
import { Link } from 'react-router-dom';
import BlogMenu from './BlogMenu';
import about_me_pic from "../img/me.jpeg"
import CountUp from 'react-countup'
import pic1 from "../img/argentina_04.jpg"
import pic2 from "../img/buenos_aires01.jpg"
import pic3 from "../img/laplata.jpg"
import dog1 from "../img/dog1.JPG"
import dog2 from "../img/dog2.JPG"
import dog3 from "../img/dog3.JPG"
import dog4 from "../img/dogs4.jpg"
import dog5 from "../img/dogs5.jpg"
import dog6 from "../img/dogs6.jpg"
import dog7 from "../img/dogs7.jpg"
import dog8 from "../img/dogs8.jpg"
import dog9 from "../img/dogs9.jpg"
import mate from "../img/mate_1.jpg"
import whole_map from "../img/whole_map.png"
import quesadillas from "../img/quesadillas.jpg"

import OwlCarousel from 'react-owl-carousel';
import 'owl.carousel/dist/assets/owl.carousel.css';
import 'owl.carousel/dist/assets/owl.theme.default.css';
import Footer from "./Footer"

class About_me extends React.Component {  

  state= {
    responsive:{
        0: {
            items: 1,
        },
        450: {
            items: 2,
        },
        600: {
            items: 3,
        },
        1000: {
            items: 4,
        },
    },
}

render(){
    return (
    <div className="about_me"> 
      <BlogMenu />

      <div className="about_me_pic">
        <h1>Обо мне</h1>
      </div>


      <div className="about_me_content grid"> 
        <div className="about_me_info"> <h2><CountUp end={36} duration={10}/></h2> <p>стран </p><img src={whole_map} /></div>
        <div className="about_me_info"> <h2><CountUp end={8} duration={10}/></h2> <p>лет в путешествии </p>  <img src={about_me_pic} /></div> 
        <div className="about_me_info"> <h2><CountUp end={1849} duration={7}/></h2> <p>выпитых мате</p>  <img src={mate} /></div>
        <div className="about_me_info"> <h2><CountUp end={1305} duration={7}/> </h2> <p>съеденных quesadiilas</p>  <img src={quesadillas} /></div>
      </div>

    

      <div className="my_fav">
        <h1>Мои любимые записи</h1>
        <div className="fav_articles grid">
        <div className="fav_article">
          <img src={pic1} />
          <h2> <a href="/articles/moi-prekrasnyi-sosed-i-pechene">Мой прекрасный сосед и печенье</a></h2>
        </div>
        <div className="fav_article">
        <img src={pic2} />
        <h2> <a href="/articles/proletel-odin-mesyac">Пролетел один месяц</a></h2>
        </div>
        <div className="fav_article">
        <img src={pic3} />
        <h2> <a href="/articles/pervyi-zakat-v-gorode-la-plata">Первый закат в городе Ла-Плата</a></h2>
        </div>
        </div>
      </div>

      <div className="dogs_wrapper">
      <h1>Собаки, которых я встретила в пути:</h1>
      <div className="dogs_gallery">
      <OwlCarousel 
        className="owl-theme"
        loop={true}
        autoplayHoverPause={true}
        margin={10}
        nav={true}
        autoplay={true}
        autoplayTimeout={2000}
        responsive={this.state.responsive} >
        

        
        
        <div className="fav_dog">
          <img src={dog1} />
        </div>
        <div className="fav_dog">
        <img src={dog2} />
        </div>
        <div className="fav_dog">
        <img src={dog3} />
        </div>
        <div className="fav_dog">
          <img src={dog4} />
        </div>
        <div className="fav_dog">
        <img src={dog5} />
        </div>
        <div className="fav_dog">
        <img src={dog6} />
        </div>
        <div className="fav_dog">
          <img src={dog7} />
        </div>
        <div className="fav_dog">
        <img src={dog8} />
        </div>
        <div className="fav_dog">
        <img src={dog9} />
        </div>
        </OwlCarousel>
        </div>
     </div>
     <Footer />
     </div>

    )
}
  
}

export default About_me