import React from 'react'


import BlogMenu from '../BlogMenu';
import MapMexico from "../MapMexico"
import Mexico from "../Mexico"
import Footer from "../Footer"

class ArgentinaComponent extends React.Component {  
   render() {
    return (
      <div>
      <BlogMenu />
     
       <div className="note">
          <div class="logo_text">
              <h3>Ксения - путешественница</h3>
              <h4>Истории о моих путешествиях</h4>
          </div>
        <div className="country_grid">
        <div>  <Mexico /> </div> 
        <div><MapMexico /></div>
        </div>
     </div>
     <Footer />
     </div>
    )
   }

  }


export default ArgentinaComponent