import React from 'react'


import marked from 'marked';
import ArticleListItem from "../blog/ArticleListItem"
import BlogMenu from './BlogMenu';
import MapArgentina from "../blog/MapArgentina"
import client from "../blog/utils/client"
import BlogContent from "./BlogContent"

class Random extends React.Component {  
    state = {
    isLoading: true,
    content: null, 
    quote: null,
    slug: null
  }  

  random = Math.floor(Math.random()*12);

  componentDidMount() {
   
    client.getEntries({
      "content_type":"blogPost",
      "fields.id": this.random 
    }
    ).then((response) => {
      if(response.items[0])  {
        this.setState({content: response.items[0].fields.content,
          slug: response.items[0].fields.slug},() =>  console.log(this.state))
          
          let strTrimmed = this.state.content.trim()
          let str = strTrimmed.split(".")
          let randomQuoteNumber = Math.floor(Math.random()*str.length)
          console.log(randomQuoteNumber)
          let randomQuote = str[randomQuoteNumber] +  "." + str[randomQuoteNumber+1] + "." + str[randomQuoteNumber+2]
          this.setState({quote: randomQuote}, console.log(this.state))
      }
 return
    }
    )}

    render() {
    let content = this.state.quote
    
    return (
      <div>
       <div className="random">
         { !content || content.includes("undefined") || content.length <= 5 ? 
         <p>но одного взгляда его мне хватило, чтобы заставить меня снова чувствовать эту тоску. в Никарагуа можно увидеть рассвет на Атлантическом океане, потом поехать на Тихий океан и там увидеть закат. в тот же самый день</p> : 
         <p>{content}</p> }
    { !content || content.includes("undefined") || content.length <= 5 ? null : <div> <a href={`/articles/${this.state.slug}`}>Читать дальше</a> </div>}
         </div>
      </div>
    
    )
  }
}

export default Random